code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background-color: #333436;
}

img {
  overflow: hidden;
}

.Welcomepage {
  padding-top: 10rem;
}

.input-icon {
  width: 4rem;
  min-width: 1.5rem;
}

.inputcontainerBG {
  background-color: #272525fa;
}

.text_input_container button {
  color: #ffff;
  border: none;
  background: rgb(49, 49, 49);
  border-radius: 40px;
  padding: 10px 14px;
  position: relative;
  margin-left: -2rem;
  border: dotted;
}

.text_input {
  border: none;
  border-radius: 40px;
  width: 50%;
  height: 35px;
  margin-top: 7px;
}

.search_input_top_img img {
  width: 30%;
  position: relative;
  margin-top: -3rem;
}

.croppedimgInput {
  overflow: hidden;
}

@media (min-width: 767px) {
  .search_input_top_img img {
    width: 15%;
  }
}

@media (max-width: 767px) {
  .text_input {
    width: 85% !important;
  }

  .search_input_top_img {
    margin-top: 4rem;
  }

  .search_input_top_img img {
    margin-top: -2rem;

  }

  .input-icon {
    width: 3rem;
  }
  #input_page_top_logo {
    display: flex;
    justify-content: center !important;
  }
  #input_page_relative_img {
    min-width: 150px !important;
  }
}


/* Start Button welcome page */
.StartBtn {
  --c: #fff;
  background: linear-gradient(90deg, #0000 33%, #fff5, #0000 67%) var(--_p, 100%)/300% no-repeat,
    #53565e;
  /* background color */
  color: #0000;
  border: none;
  transform: perspective(500px) rotateY(calc(2deg*var(--_i, -1)));
  text-shadow: calc(var(--_i, -1)* 0.08em) -.01em 0 var(--c),
    calc(var(--_i, -1)*-0.08em) .01em 2px #0004;
  outline-offset: .1em;
  transition: 0.3s;

}

.StartBtn:hover,
.StartBtn:focus-visible {
  --_p: 0%;
  --_i: 1;
}

.StartBtn:active {
  text-shadow: none;
  color: var(--c);
  box-shadow: inset 0 0 9e9q #0005;
  transition: 0s;
}

.StartBtn {
  font-weight: bold;
  font-size: 2rem;
  margin: 0;
  cursor: pointer;
  padding: .1em .3em;
}